import React from 'react';
import StarRatingComponent from 'react-star-rating-component';

class AddReviewModal extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			rating: 0
		}
	}

	componentDidMount() {
	}

	onStarClick(next) {
		this.setState({rating: next});
	}

	render() {
		return (
			<div className="AddReviewModal">
				<div className="modal fade" id="addReviewModal" tabIndex="-1" role="dialog" aria-labelledby="addReviewModalLabel" aria-hidden="true">
					<div className="modal-dialog" role="document">
						<div className="modal-content add-recipe">
							<div id="add-recipe-bg"></div>
							<div className="modal-header">
								<button type="button" className="close" data-dismiss="modal" aria-label="Close">
									<span aria-hidden="true">&times;</span>
								</button>
								<h4 className="modal-title" id="addReviewModalLabel">nh gi cng thc: {this.props.recipeTitle}</h4>
							</div>
							<div className="modal-body">
								<form className="container-fluid clearfix" onSubmit={this.props.handleSubmit}>
									<div className="form-group row">
										<label className="col-sm-3 form-control-label">Bn cho cng thc ny my sao?</label>
										<div className="col-sm-9">
											<StarRatingComponent
												name="recipeRating"
												starCount={5}
												value={this.state.rating}
												onStarClick={this.onStarClick.bind(this)}
											/>
										</div>
									</div>
									<div className="form-group row">
										<label className="col-sm-3 form-control-label">Nhn xt ca bn v cng thc ny?</label>
										<div className="col-sm-9">
											<textarea rows="4" id="recipeReview" name="recipeReview" className="form-control"></textarea>
										</div>
									</div>
									<input type="hidden" id="review-star-rating" value={this.state.rating} />
									<button type="submit" className="btn btn-sm btn-primary recipe-submit pull-right">Nhn xt</button>
								</form>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default AddReviewModal;